.hamburger-inner {
  background-color: map-get($colors, "grey-dark") !important;
  border-radius: 0 !important;
  &::before,
  &::after {
    background-color: map-get($colors, "grey-dark") !important;
    border-radius: 0 !important;
  }
}

// Body modifiers for header
.header {

  & .main-content > section:first-child {
    padding-top: 120px;
  }

  &-transparent {
    & .nav-link {
      color: $white !important;
    }
    & > header {
      background-color: transparent !important;
    }
  }

  &-white {
    & .nav-link {
      color: $black !important;
    }
    & > header {
      background-color: $white !important;
    }
  }

  &-shadow {
    & > header {
      box-shadow: 0px 3px 10px rgba(0, 0, 0, 0.1);
    }
  }

}
// Header modifiers
header.header {
  &.is-absolute {
    position: absolute;
    width: 100%;
  }
  &.is-fixed-top {
    position: fixed;
    top: 0;
    right: 0;
    left: 0;
    z-index: 1030;
  }
}

// Header search input
.header-search {
  &-input {
    background-color: $white !important;
    border: 1px solid $white !important;
    border-radius: 15px 0px 0px 15px !important;
  }
  &-btn {
    border-radius: 0px 15px 15px 0px !important;
    background-color: $white !important;
  }
}

// Waves
.header-svg-wave {
  &-one {
    fill: black;
    opacity:.05;
  }
  &-two {
    fill: $white;
    opacity:1;
  }
}

#toc {
  position: relative;
  bottom: 8px;
  outline: none;
  float: right;
}
#toc_expanded {
  position: relative;
  bottom: 8px;
  outline: none;
  float: right;
  transform: rotate(180deg);
}


// Mobile menu
.mobile-menu {
  // Closed
  @include size(560px, 65px);
  z-index: 9999;
  position: fixed;
  right: 28px;
  top: 9px;
  visibility: hidden;

  @media(max-width: map-get($grid-breakpoints, "lg") - 1px) {
    visibility: visible;
  }

  & > button {
    cursor: pointer;
  }

  & > .menu, & > .submenu {
    position: absolute;
    right: -1000px;
    top: 62px;
    text-align: right;
    transition: all .5s ease-out;
    & ul {
      list-style-type: none;
      margin: 0;
      padding: 0;
      & li {
        & > a {
          text-decoration: none;
        }
      }
    }
  }

  & > .submenu {
    text-align: left !important;
    width: 100%;
  }

  .white-panel {
    content: "";
    position: absolute;
    width: 200%;
    background-color: $white;
    transition: all .3s ease-out;
    transform-origin: 0 100%;
    &-top {
      height: 600px;
      bottom: 600px;
      left: -120px;
      // background-color: rgba(255,0,0,0.5);
    }
    &-right {
      height: 200%;
      bottom: -192px;
      left: 120%;
      // background-color: rgba(0,255,0,0.5);
    }
  }

  //Opened
  &.is-active {
    height: 560px;

    .white-panel {
      &-top {
        transform: rotate(45deg);
        box-shadow: 0px 1px 10px rgba(0, 0, 0, .1);
      }
      &-right {
        transform: rotate(-45deg);
      }
    }

    & > .menu {
      right: 31px;
    }

    & > .submenu {
      &.is-expanded {
        right: -27%;
        @media(max-width: map-get($grid-breakpoints, "sm") - 1px) {
          right: -45%;
        }
      }
    }

    &.is-expanded {
      & > .menu {
        right: -1000px !important;
      }
      .white-panel {
        &-top {
          transform: rotate(90deg) !important;
          left: 27% !important;
          background-color: #f6f7fa;
          @media(max-width: map-get($grid-breakpoints, "sm") - 1px) {
            left: 45% !important;
          }
        }
        &-right {
          transform: rotate(0deg) !important;
        }
      }

    }

  }


}
