.dropdown-style-1 {

  &.show {
    &::after {
      @include size(24px, 12px)
      background: url(/static/images/arrow-white-top.png) no-repeat;
      position: absolute;
      bottom: -4px;
      left: calc(50% - 12px);
      content: '';
    }
  }

  & > .dropdown-menu, & > .dropdown-mobile-menu {
    border-width: 0;
    border-radius: 10px;

    & > .dropdown-header {
      clear: both;
      min-width:370px;
      margin: 0.25em;

      & > .dropdown-icon {
        float:left;
      }

      & > .dropdown-text {
        & > a {
          font-size: 16px;
          font-weight: 600;
          text-decoration: none;
        }
        & > p {
          font-size: 15px;
          font-weight: 400;
          line-height: 130%;
          color: #8595a4;
        }
      }

    }

    & > ul.list-group {
      background-color: #f6f7fa;
      border-radius: 0px 0px 10px 10px;

      & > a {
        color: #48525a !important;
        text-decoration: none !important;
        & > li:hover {
          background-color: shade(#f6f7fa, 5);
        }

        & > li {
          background-color: inherit;
          & span {
            color: #48525a !important;
            text-decoration: none !important;
          }
        }

      }

    }
  }
}
