// home
.homepage-service {

  @media(max-width: map-get($grid-breakpoints, "sm")) {
    & > p, & > div {
      display: none;
    }

    &-container {
      & > .row > div {
        min-height: 0em !important;
        border-bottom: 2px solid rgba(0,0,0,0.1);
        &:first-child {
          border-top: 2px solid rgba(0,0,0,0.1);
        }
      }
    }

    & > h5 > i {
      position: relative;
      opacity: 1 !important;
      left: 0em !important;
      float: right;
      font-size: 1.5em;
    }

  }

  &-container {

    & > .row > div {
      min-height: 14em;
      padding-top: 2em;
      padding-bottom: 2em;
      &:hover {
        & > div.homepage-service > h5 > i {
          position: relative;
          opacity: 1;
          left: 0;
        }
      }
    }

  }

  & > p {
    margin: 1rem 0 1rem !important;
    font-size: 1rem !important;
  }

  & > div {

    width: 90%;
    border-bottom: 2px solid rgba(0,0,0,0.1);
    position: absolute;
    bottom: 0;

    & > a {
      text-transform: uppercase;
      & > i {
        position: relative;
        top: 4px;
        transform: rotate(90deg);
        padding-right: 0.45em
      }
    }

  }

  & > h5 {
    & > a {
      color: $black !important;
    }
    i {
      position: relative;
      transition: all .25s ease-out;
      opacity: 0;
      left: -1em;
    }
  }


}
