.mobile-menu {
  width: 560px;
  height: 65px;
  z-index: 9999;
  position: fixed;
  right: 28px;
  top: 9px;
  visibility: hidden;
}

@media (max-width: 991px) {
  .mobile-menu {
    visibility: visible;
  }
}

.mobile-menu > button {
  cursor: pointer;
}

.mobile-menu > .menu, .mobile-menu > .submenu {
  position: absolute;
  right: -1000px;
  top: 62px;
  text-align: right;
  transition: all .5s ease-out;
}

.mobile-menu > .menu ul, .mobile-menu > .submenu ul {
  list-style-type: none;
  margin: 0;
  padding: 0;
}

.mobile-menu > .menu ul li > a, .mobile-menu > .submenu ul li > a {
  text-decoration: none;
}

.mobile-menu > .submenu {
  text-align: left !important;
  width: 100%;
}

.mobile-menu .white-panel {
  content: "";
  position: absolute;
  width: 200%;
  background-color: white;
  transition: all .3s ease-out;
  -webkit-transform-origin: 0 100%;
  transform-origin: 0 100%;
}

.mobile-menu .white-panel-top {
  height: 600px;
  bottom: 600px;
  left: -120px;
}

.mobile-menu .white-panel-right {
  height: 200%;
  bottom: -192px;
  left: 120%;
}

.mobile-menu.is-active {
  height: 560px;
}

.mobile-menu.is-active .white-panel-top {
  -webkit-transform: rotate(45deg);
  transform: rotate(45deg);
  box-shadow: 0px 1px 10px rgba(0, 0, 0, 0.1);
}

.mobile-menu.is-active .white-panel-right {
  -webkit-transform: rotate(-45deg);
  transform: rotate(-45deg);
}

.mobile-menu.is-active > .menu {
  right: 31px;
}

.mobile-menu.is-active > .submenu.is-expanded {
  right: -27%;
}

@media (max-width: 575px) {
  .mobile-menu.is-active > .submenu.is-expanded {
    right: -45%;
  }
}

.mobile-menu.is-active.is-expanded > .menu {
  right: -1000px !important;
}

.mobile-menu.is-active.is-expanded .white-panel-top {
  -webkit-transform: rotate(90deg) !important;
  transform: rotate(90deg) !important;
  left: 27% !important;
  background-color: #f6f7fa;
}

@media (max-width: 575px) {
  .mobile-menu.is-active.is-expanded .white-panel-top {
    left: 45% !important;
  }
}

.mobile-menu.is-active.is-expanded .white-panel-right {
  -webkit-transform: rotate(0deg) !important;
  transform: rotate(0deg) !important;
}

