$timeline-slide-bg-color: tint($secondary, 40);

.tl-szkolenia-krok__header {
    overflow-x: auto
}

.tl-gradient {
  background: linear-gradient(180deg, $secondary 0%, $white 100px);
}

.tl-nav-szkolenia-krok {
    display: flex;
    justify-content: center;
    align-items: flex-end;
    padding-top: 58px;
    position: relative;
    min-width: 1440px;
    list-style-type: none;
    margin-bottom: 0px;

    &:before {
        @include size(100%, 2px);
        content: "";
        position: absolute;
        left: 0;
        top: 22px;
        background: linear-gradient(to right, #006EFF 0%, #1f7fff 17.17%, #338aff 33.36%, #4a97ff 50%, #5ba0fc 66.58%, #73afff 82.87%, #96c3ff 100%);
    }

    &__item {
        height: 66px;
        font-size: 12px;
        line-height: 16px;
        text-transform: uppercase;
        font-weight: bold;
        letter-spacing: 1.4px;
        border-top-left-radius: 20px;
        border-top-right-radius: 20px;
        margin: 0 30px;
        position: relative;
        @media (max-width: 1350px) {
            & {
                margin: 0 20px
            }
        }
    }

    &__link {
        display: flex;
        justify-content: center;
        align-items: center;
        text-align: center;
        @include size(100%);
        padding-left: 32px;
        padding-right: 32px;
        cursor: pointer;
    }

    &__blob {
        @include size(18px);
        background-color: #fff;
        position: absolute;
        left: 50%;
        transform: translateX(-50%);
        border-radius: 100px;
        top: -44px;
        transition: all ease-in-out 200ms;
        background-color: #000;
        cursor: pointer;

        &:before,
        &:after {
            position: absolute;
            content: "";
            top: 50%;
            left: 50%;
            transform: translate(-50%, -50%);
            border-radius: 100px;
            transition: all ease-in-out 200ms
        }

        &:before {
            @include size(14px);
            background-color: #fff
        }

        &:after {
          @include size(4px);
          background-color: $secondary;
        }

        &__inner {
            opacity: 0;
            @include size(28px);
            position: absolute;
            top: 50%;
            left: 50%;
            transform: translate(-50%, -50%);
            border-radius: 100px;
            border: 2px solid transparent;
            transition: all ease-in-out 200ms
        }
    }

    &__decor {
        display: none;
        @include size(25px);
        position: absolute;
        bottom: 0;
        &:before {
            @include size(50px);
            content: "";
            position: absolute;
            border-radius: 100px;
            bottom: 0;
            background: #fff;
        }
    }

    &__decor--left {
        left: -24px;
        &:before {
            left: -26px;
        }
    }

    &__decor--right {
        right: -24px;
        &:before {
            right: -26px
        }
    }

    &__item.active &__blob {
        width: 36px;
        height: 36px;
        top: -53px
    }

    &__item.active &__blob:before {
        width: 32px;
        height: 32px
    }

    &__item.active &__blob:after {
        width: 10px;
        height: 10px
    }

    &__item.active &__blob__inner {
        opacity: 1
    }

    &__item.active &__decor {
        display: block
    }

    @for $num from 1 through 7 {
      &__item--#{$num}.active {
          color: black;
          background: linear-gradient(180deg, $timeline-slide-bg-color 0%, $timeline-slide-bg-color 100%)
      }

      &__item--#{$num}.active &__blob:after {
          background: linear-gradient(135deg, $primary 0%, $secondary 100%)
      }

      &__item--#{$num} &__decor {
          background: linear-gradient(180deg, $timeline-slide-bg-color 0%, $timeline-slide-bg-color 100%);
      }

      &__item--#{$num} &__blob {
          background: linear-gradient(135deg, $primary 0%, $secondary 100%);
          box-shadow: 0px 1px 1px $primary
      }

      &__item--#{$num} &__blob__inner {
          border-color: $secondary
      }
    }
}
