.zaufali {

  &-container {

    & > .tabs {

      & > .tab {

        border-bottom: 3px solid transparent;
        cursor: pointer;

        &.is-active {
          border-color: $secondary;
          & > h4 {
            color: $secondary;
          }
        }

        & > h4 {
          font-weight: 600;
          line-height: 1.88;

          @media(max-width: map-get($grid-breakpoints, "lg") - 1px) {
            font-size: 0.9em !important;
          }

        }

      }

    }

  }

  &__title {

  }

}
