@mixin position($type, $top: 0, $bottom: 0, $right: 0, $left: 0) {
  position: $type;
  @if $top != 0 {
    top: $top;
  }
  @if $bottom != 0 {
    bottom: $bottom;
  }
  @if $right != 0 {
    right: $right;
  }
  @if $left != 0 {
    left: $left;
  }
}

@mixin size($width, $height: $width) {
  width: $width;
  height: $height;
}

@mixin bg-gradient($name, $degrees, $color1, $color2) {
  .bg-gradient-#{$name} {
    background: linear-gradient(#{$degrees}, #{$color1} 0, #{$color2} 100%) !important;
  }
}

@mixin google-font($family) {
  @import url('https://fonts.googleapis.com/css?family=#{$family}&display=swap');
}
